import { mapState } from 'vuex';
import chatName from '@/components/chats/assets/name.vue'
import chatIcon from '@/components/chats/assets/icon.vue'
import userView from '@/components/user/view/pnuser/index.vue'

//
import { Carousel, Slide } from 'vue-carousel';
//

export default {
    name: 'chatPreview',
    props: {
        chat : Object,
        usersinfo : Array
    },

    components : {
        userView,
        chatName,
        chatIcon,

        Carousel, 
        Slide
    },

    data : function(){

        return {
            loading : false,
            usersinfoLocal : []
        }

    },

    computed: mapState({
        auth : state => state.auth
    }),

    mounted : function(){
        this.setUsersInfo()
    },

    methods : {
        setUsersInfo : function(){

            if (this.chat){
                var members =_.filter( _.map(_.filter(this.chat?.currentState.members || {}, (m) => {

                    return  this.$f.getmatrixid(m.name) != this.core.user.userinfo?.id
        
                }), m => {
                    return this.$f.deep(this, '$store.state.users.' + this.$f.getmatrixid(m.name)) || null
                }), m => {
                    return m
                })

                this.usersinfoLocal = members
            }
            else{
                this.usersinfoLocal = this.usersinfo
            }

        }
    },
}