import {mapState} from 'vuex';


export default {
  name: 'contactsPreview',
  props: {
    contact: {
      type: Object,
      default: () => {
         return {
            message: "hello"
        }
      }
    }
  },

  components: {},

  data: function () {

    return {
      loading: false
    }

  },

  computed: mapState({
    auth: state => state.auth,

  }),

  methods: {},
}