import {mapActions, mapState} from 'vuex'
import preview from '@/components/contacts/preview/index.vue'

export default {
  name: "contactsList",
  data: function () {

    return {
      loading: false,
    }

  },
  components: {
    preview
  },

  props: {
    mode: {
      default: "default",
      type: String
    },
    user_data: {},
    title: String,
  },
  created: () => {

  },

  watch: {
    //$route: 'getdata'
  },

  beforeRouteEnter(to, from, next) {
    next()
  },

  beforeRouteUpdate(to, from, next) {
    next()
  },

  computed: mapState({
    auth: state => state.auth,

    ...mapState([
      'contactsMap'
    ])

  }),

  methods: {
    select: function (contact) {
      console.log('select', contact)
      this.$emit('select', contact)
    }
  },

  mounted() {

  }
}