import { mapState } from 'vuex';

export default {
    name: 'userpic',
    props: {
        userinfo : Object,
        unseen : Number
    },

    data : function(){
        return {
        }
    },

    created : () => {

    }, 

    computed: mapState({
        auth : state => state.auth,
        info : state => {
            
        },

        picture : function(state){

            var pc = this.userinfo ? this.userinfo.image || '' : ''

            return pc
        },  

        fl : state => {

            return [
                (this.userinfo ? this.userinfo.name || '' : '')
            ]
            .map(w => !w ? '' : w.substr(0, 1))
            .join('').toUpperCase()
        
        }


    }),

    methods : {
       
    },
}
