var _ = require('lodash');

class User {
    constructor(core, p){
        if(!p) p = {}

        this.core = core
        this.state = 0

        this.userinfo = {
            image : '',
            name : '',
            id : ''
        }
        
    }

    setCredentials (credentials){
        this.credentials = credentials
    }

    setUsersInfo (usersinfo){
        _.each(usersinfo || [], v => {
            this.core.store.commit('SET_USERINFO', v)
        })
    }

    setContacts (usersinfo){
        
        this.core.store.commit('SET_CONTACTS', usersinfo)
        
    }

    matrixId(id){
        return '@' + id + ':matrix.pocketnet.app'
    }
}

export default User