import {mapState} from 'vuex';

import events from '@/components/events/list/index.vue'

export default {
  name: 'chatList',
  props: {
    chat: Object,
    to: String,
    prev: {},
    putMeta: {},
    filePrev: [Object, File],
    imagePreview: {}
  },

  components: {
    events
  },

  data: function () {

    return {
      loading: false,
      scrolling: false,
      scrollingTo: 0,
      cancelNextScroll: false,
      timeline: null,
      wh: document.documentElement.clientHeight,

      c_uspbottomHeight: 0,
      c_frombottomscroll: 0,
      c_bottomWndOffset: 80,
      c_topWndOffset: 55,

      p_b: false,
      p_f: false,

      updateTimeout: null,

      events: [],

      firstPaginate: true

    }

  },

  mounted: function () {
    this.init()
  },

  watch: {
    //$route: 'getdata'
  },
  computed: mapState({
    auth: state => state.auth,
    // events: function () {
    //   if (this.chat.roomId) {
    //     var events = ((this.$store.state.events[this.chat.roomId] || {}).timeline || [])

    //     return events

    //   }

    // },

    eventsTypes: function () {
      var types = {
        'm.room.message': true,
        'm.room.image': true,
        'm.room.file': true
      }

      if (_.toArray(this.chat?.currentState.members || {}).length > 2) {
        types['m.room.member'] = true
      }

      return types
    }

  }),

  created() {
    window.addEventListener('wheel', this.wheel);

    this.update = setInterval(this.update, 50)


  },
  destroyed() {
    window.removeEventListener('wheel', this.wheel);

    clearInterval(this.update)
  },


  methods: {


    init: function () {


      this.loading = true

      var timeline = this.chat.getLiveTimeline();

      var ts = timeline.getTimelineSet()

      this.timeline = new this.core.mtrx.sdk.TimelineWindow(this.core.mtrx.client, ts)

      this.uspBottomHeight()
      this.fixBottomScroll()

      this.timeline.load(null, window.clientHeight).then(r => {

        this.loading = false;

        this.uspBottomHeight()
        this.autoPaginateAll()


      })

    },

    getEvents: function () {
      var events = this.timeline.getEvents()
      events = _.filter(events, e => {
        var type = e.event.type;

        return !this.eventsTypes || this.eventsTypes[type]
      })
      console.log('events', events)
      // console.log(events[133].flagCancelled, "first event")


      return events;
    },

    autoPaginate: function (direction) {

      if (this.needLoad(direction)) {
        this.paginate(direction)
      }

    },


    paginate: function (direction) {

      if (!this.loading && this.timeline && !this['p_' + direction]) {

        if (this.timeline.canPaginate(direction) && window.scrollY < 700) {

          this['p_' + direction] = true

          let count = this.firstPaginate ? 25 : 12;   

          this.timeline.paginate(direction, count).then(e => {

            console.log('this.events1', this.events);

            this.events = this.getEvents();

            console.log('this.events2', this.events);

            this['p_' + direction] = false;

            const lastScroll = window.scrollY;
            const lastHeight = document.body.scrollHeight;
            
            this.$nextTick(() => {

              if (direction === 'b'){

                let scrolling = (this.$refs.events.scrollHeight - lastHeight + 76) + lastScroll;

                if (this.firstPaginate){
  
                  scrolling = this.$refs.events.scrollHeight;
                  this.firstPaginate = false;
                }
       
                window.scroll({
                  top: scrolling,
                  behavior: 'instant'
                })
                // this.spacersHeight()

              } else {

                window.scroll({
                  top: this.$refs.events.scrollHeight,
                  behavior: 'instant'
                })
              }



            })


          })
        } else {
        }


      }

    },

    autoPaginateAll: function () {

      this.autoPaginate('b')
      this.autoPaginate('f')
    },

    needLoad: function (direction) {
      var s = this.scroll()
      var p = this.eventsPosition();
      var r = false
      var safespace = this.wh * 3

      if (p) {
        if (direction == 'b') {
          if (p.top + safespace >= s) {
            r = true
          }

        } else {
          if (Math.max(p.top + p.height - safespace) <= s) r = true
        }
      }

      return r
    },

    //////////////

    readEvent: function (event) {

      var byme = this.core.mtrx.me(event.event.sender)

      if (byme) return

      /// todo check user and check readed

      this.core.mtrx.isReaded(event, true).then(readed => {

        if (!readed) {
          this.core.mtrx.client.sendReadReceipt(event)
        }
      })


    },

    readFirst: function () {
      var events = this.timeline.getEvents();

      this.readEvent(events[0])
    },

    readLast: function () {
      var events = this.timeline.getEvents();

      this.readEvent(events[events.length - 1])
    },

    readEvents: function (events) {
      _.each(events, (e) => {
        this.readEvent(e)
      })
    },

    readAll: function () {
      var events = this.timeline.getEvents();

      _.each(events, (e) => {
        this.readEvent(e)
      })

    },

    //////////////

    scroll: function () {
      return window.scrollY
    },

    eventsPosition: function () {
      return this.$refs.events.getBoundingClientRect()
    },

    workPosition: function () {
      return this.$refs.work.getBoundingClientRect()
    },

    uspBottomHeight: function () {

      var p = this.eventsPosition() || {}

      this.c_uspbottomHeight = this.wh - (p.height || 0) - this.c_topWndOffset - this.c_bottomWndOffset

      if (this.c_uspbottomHeight < 0) this.c_uspbottomHeight = 0

      this.$refs['uspbottom'].style.height = this.c_uspbottomHeight + 'px'

    },

    spacerHeight: function (direction) {

      var spacercpace = this.wh * 2
      var style = this.$refs['direction_' + direction].style

      var spacerneed = this.timeline.canPaginate(direction)

      if (direction == 'b')
        spacercpace = spacercpace - this.c_uspbottomHeight

      if (spacerneed) style.height = spacercpace + 'px'
      else style.height = '0px'

      if (direction == 'b') {

        this.correctBottomScroll()
      }

    },

    spacersHeight: function () {
      this.spacerHeight('b')
      this.spacerHeight('f')
    },

    wheel: function (e) {

      if (this.scrolling) {
        e.preventDefault();
      }

    },

    eventsUpdated: function (events) {
      this.readEvents(events)
      // this.spacersHeight()
    },

    update: function (e) {

      console.log('update');

      if (!this.scrolling) {

        this.uspBottomHeight()
        this.fixBottomScroll()
        this.autoPaginateAll()
      } else {
        //e.preventDefault()
      }
    },

    fixBottomScroll: function () {

      var p = this.workPosition()

      var scrollingTo = this.scrollingTo

      if (!this.scrolling) {
        scrollingTo = this.scroll()
      }

      var s = p.height - scrollingTo - this.wh + this.c_topWndOffset + this.c_bottomWndOffset

      if (s < 0) s = 0

      //console.log('this.c_frombottomscroll', s, scrollingTo, this.scroll())

      this.c_frombottomscroll = s
    },

    correctBottomScroll: function () {

      var p = this.workPosition()

      var s = p.height + this.c_topWndOffset + this.c_bottomWndOffset - this.wh - this.c_frombottomscroll


      this.scrollTo(s)
    },

    scrollTo: function (s) {
      if (s < 0) s = 0

      if (this.scrolling)
        cancelAnimationFrame(this.scrolling)

      this.scrollingTo = s
      //console.log('SCROLLTO-1', s)
      this.scrolling = requestAnimationFrame(() => {


        window.scroll({
          top: s,
          behavior: 'instant'
        })

        //console.log('SCROLLTO', this.c_frombottomscroll, s, this.scroll())

        this.scrollingTo = 0
        this.scrolling = null
        //this.cancelNextScroll = true

      })
    },
    openImgGallery: function (img) {
      this.$emit('openImg', img)
    }
  },
}