import {mapState} from 'vuex';
import list from '@/components/contacts/list/index.vue'
import _ from "underscore";
export default {
  name: 'contacts',
  props: {
    mode: {
      default: "default",
      type: String
    },
    title: String
  },

  components: {
    list
  },

  data: function () {

    return {
      loading: false,
      fromSearch: [],
      myContacts: Object,
      inputText: ''
    }

  },

  created: () => {

  },

  watch: {
    //$route: 'getdata'
  },
  computed: mapState({
    auth: state => state.auth,
    ...mapState([
      'contactsMap'
    ])
  }),

  methods: {
     search: function (text) {
      this.inputText = text
      this.core.user.searchContacts(text).then(users => {
        this.fromSearch = users
        this.myContacts = _.where(this.contactsMap, {name: text})
      })
    },

    select: function (contact) {
      this.$emit('select', contact)
    }
  },
}