import * as sdk from "matrix-js-sdk";
import axios from 'axios'
import MTRXKIT from "./mtrxkit";
import f from "./functions";


var _ = require('lodash');

class MTRX {

  constructor(core, p) {
    if (!p) p = {}

    this.core = core
    this.baseUrl = p.baseUrl
    this.ready = false
    this.error = false
    this.kit = new MTRXKIT(this.core, p)
    this.sdk = sdk
  }

  setCredentials() {
    return this.core.user.matrixCredentials().then(credentials => {


      this.credentials = credentials
      return Promise.resolve(credentials)
    })

  }

  getAccess() {
    return this.setCredentials().then(credentials => {
      const url = { baseUrl: this.baseUrl }
      const client = sdk.createClient(url)
      const user = credentials.user.toLowerCase()
      const auth = { type: "m.login.dummy" }
      async function matrixSdk() {
        try {
          let loginData = await client.login("m.login.password", { user: user, password: credentials.password })
          return loginData
        } catch (e) {
          let checkUser = await client.isUsernameAvailable(user)
          let registerData = await client.register(user, credentials.password, credentials.sessionId, auth)

          return registerData
        }
      }
      return matrixSdk()
    }).then(data => {
      this.access = {
        baseUrl: this.baseUrl,
        userId: data.user_id,
        accessToken: data.access_token
      }
      return Promise.resolve(this.access)
    })

  }

  createClient() {
    return this.getAccess().then(() => {



      this.access.timelineSupport = true

      this.client = sdk.createClient(this.access);
      this.store = this.client.store


      console.log('this.client', this.client, sdk)

      return this.client.startClient({ initialSyncLimit: 1 }).then(r => {

        this.ready = true


        return Promise.resolve(this.ready)

      }).catch(e => {

        this.error = e

        this.ready = true

        return Promise.reject(e)

      })

    })

  }

  wait() {
    return f.pretry(() => {
      return this.ready
    }).then(() => {

      if (this.error) {
        return Promise.reject(this.error)
      }

      return Promise.resolve()

    })
  }

  me(id) {
    return f.getmatrixid(id) == this.core.user.userinfo?.id
  }

  reciepts(event){
    var room = null;

    return f.pretry(() => {

        var rooms = f.deep(this.core, 'mtrx.store.rooms')
        room = rooms[event.getRoomId()]

        return room

    }).then(() => {

      return room.getReceiptsForEvent(event)

    })
  }

  isReaded(event, me){

    return this.reciepts(event).then(reciepts => {
      return Promise.resolve (

        _.find(reciepts, reciept => {
          var m = this.me(reciept.userId)

          return reciept.type == 'm.read' && ((me && m) || (!m && !me))
        })

      )
    })


  }

  initEvents() {

    this.client.on("RoomMember.membership", (event, member) => {

      if (member.membership === "invite" && member.userId === this.userId) {

        this.core.notifier.event(event)

        //this.client.joinRoom(member.roomId).then(() => {
          //console.log("Auto-joined %s", member.roomId);
        //});

      }

    });

    this.client.on("Room.timeline", (event, member) => {


      this.core.store.dispatch('FETCH_EVENTS')

      this.core.notifier.event(event)
      //

    });

    this.client.on("RoomMember.typing", (event, member) => {

      this.core.store.dispatch('TYPING_EVENT', member)
 
    });

    this.client.on('sync', (state, prevState, res) => {

      if (state === 'PREPARED') {
        //console.log("prepared");

        this.ready = true
      }

      this.core.store.dispatch('FETCH_CHATS')
    });

  }

  init() {

    return this.createClient().then(r => {

      this.initEvents()

      return Promise.resolve()
    })
  }

  uploadContent (file){
    return this.client.uploadContent(file).then(src => {
        return Promise.resolve(this.core.mtrx.client.mxcUrlToHttp(src))
    })
  }

}

export default MTRX