<template>
  <div class="metaMsg">
    <div class="textMsg">
      <a :href="metaUrl">{{ metaUrl }}</a>
      {{ msg }}
    </div>
    <div>
      <span class="title">{{name}}</span>
      <span class="title">{{ title }}</span>
    </div>
    <div class="description">
      <span>{{ description }}</span>
    </div>
    <div 
      v-if="!video" 
      class="image"
      :style="{width: imageWidth + 'px', height: imageHeight + 'px'}"
    >
      <img :src="image" alt="meta Image" v-if="image">
    </div>
    <div v-if="video" class="video">
      <iframe width="100%" height="250px" :src="video"></iframe>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    title: String,
    msg: String,
    name: String,
    description: String,
    image: String,
    video: String,
    metaUrl: String,
    h: Number, 
    w: Number,
    initImageWidth: Number,
    imageFactor: Number,
    windowWidth: Number
  },
  data: () => {
    return {
      link: '',
      txt: String,
    }
  },
  computed: {

    imageWidth(){

      return this.windowWidth < 650 ? this.imageFactor * this.initImageWidth : this.initImageWidth;
    },

    imageHeight(){

      const ratio = this.w / this.imageWidth;

      return this.h / ratio || 300;
    }
  },
  mounted() {

  }
}
</script>
<style lang="sass" scoped>
.metaMsg
  box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 1px 3px 1px;
  max-width: 100%
  // border-left: .2em solid #0059ff
  // -webkit-box-shadow: 0px -4px 17px -12px #0059ff
  // -moz-box-shadow: 0px -4px 17px -12px #0059ff
  // box-shadow: 0px -4px 17px -12px #0059ff
  border-radius: 1em
  padding: .5em

  .textMsg
    word-break: break-all
    font-size: 0.8em
    cursor: pointer
    &:hover
     a
       display: block
       width: 100%
       height: 100%
       color: #62bcf9

    a
      font-weight: bold
      color: #0059ff

  .title
    font-weight: bold
    display: block
    font-size: 0.9em
    margin: 0.4em 0
    span
      display: block

  .description
    margin: 0.2em 0
    font-size: 0.8em
    display: flex
    justify-content: flex-start

  .image
    margin: 0.2em 0

    img
      display: block
      object-fit: contain
      border-radius: .5em
      width: 100%
      object-fit: cover
  .video
    margin: .5em 0
    iframe
      border: 0
      border-radius: .5em

</style>