import Vue from 'vue'
import Vuex from 'vuex'
import f from '@/application/functions.js'

Vue.use(Vuex);

var themes = {
    white : "White",
    black : "Dark",
    classic : "Classic"
  }
  
  var mex = {
    theme : function(state, value){
      if(themes[value]){
  
        state.theme = value
        // document.getElementById("roothtml").setAttribute("theme", state.theme);
        localStorage.setItem('theme', state.theme)
        
      }
    }
  }

let store = new Vuex.Store({
    state: {
        contacts: {},
        contactsFromMatrix: {},

        contactsMap: {},
        chats: [],
        chatsMap: {},
        events: {},
        users: {},
        typing: {},
        icon: false,
        loading: false,
        online: true,
        unauthorized: false,

        theme : 'white',
        themes : themes,

    },
    getters: {
        getConnection: state => {
            return state.localMatrixStore
        },
    },
    mutations: {

        init(state){
            mex.theme(state, localStorage.getItem('theme') || 'white')
        },
        SET_CHATS_TO_STORE(state, chats) {
            state.chats = chats;

            var chatsMap = {}

            _.each(chats, function (chat) {
                chatsMap[chat.roomId] = chat
                chatsMap[chat.info.title.replace("#", '')] = chat
            })

            state.chatsMap = chatsMap;
        },

        SET_EVENTS_TO_STORE(state, events) {
            state.events = events;
        },

        SET_TYPING_TO_STORE(state, data) {
            state.typing[data.room] || (state.typing[data.room] = {})
            state.typing[data.room][data.name] = data.typing

            function allTrue(data) {
                for (var o in data)
                    if (!data[o]) return false;

                return true;
            }

            allTrue(data.typing)

        },

        SET_CONTACTS(state, v) {
            var mp = {}

            _.each(v, function(c){
                mp[c.id] = c
            })

            state.contacts = mp

            store.commit('SET_CONTACTS_MAP')
        },

        SET_CONTACTS_FROM_MATRIX(state, v) {
            var mp = {}

            _.each(v, function(c){
                if(f.getmatrixid(c.id) != store._vm.core.user.userinfo?.id)
                    mp[c.id] = c
            })

            state.contactsFromMatrix = mp

            store.commit('SET_CONTACTS_MAP')
        },

        SET_CONTACTS_MAP(state) {

            var contactsMap = {}

            _.each(state.contacts, function (contact) {
                contactsMap[contact.id] = contact
            })

            _.each(state.contactsFromMatrix, function (contact) {
                contactsMap[contact.id] = contact
            })

            state.contactsMap = contactsMap;
        },

        SET_UNAUTHORIZED(state, v) {
            state.unauthorized = v
        },

        SET_USERINFO(state, v) {
            state.users[v.id] = v
        },

        SET_CURRENT_ROOM(state, v) {

            state.currentRoom = v
        },

        CLEAR_USERSINFO(state, v) {
            state.users = {}
        },


        theme(state, value){
            mex.theme(state, value)
        }

    },
    actions: {

        TYPING_EVENT({commit}, member) {
            let room = member.roomId
            let name = member.name
            let data = {room, name, typing: member.typing}
            commit('SET_TYPING_TO_STORE', data)
        },

        FETCH_CHATS({commit}) {

            var m_chats = f.deep(store._vm, 'core.mtrx.store.rooms') || {}


            var chats = _.map(m_chats, function (r) {
                return r.summary
            })

            store._vm.core.mtrx.kit.usersInfoForChats(m_chats).then(i => {

                i = _.filter(i, (m) => {
                    return  f.getmatrixid(m.name) != store._vm.core.user.userinfo?.id
                })

                commit('SET_CONTACTS_FROM_MATRIX', i)

                commit('SET_CHATS_TO_STORE', chats)
            })

        },


        FETCH_EVENTS({commit}) {

            var m_chats = f.deep(store._vm, 'core.mtrx.store.rooms') || {}

            var events = {}

            _.each(m_chats, function (chat) {

                events[chat.roomId] || (events[chat.roomId] = {
                    timeline: []
                })

                events[chat.roomId].timeline = events[chat.roomId].timeline.concat(chat.timeline)
                events[chat.roomId].timeline = events[chat.roomId].timeline.concat(chat.currentState.getStateEvents('m.room.member'))

                events[chat.roomId].timeline = _.map(events[chat.roomId].timeline, function (e) {
                    return e
                })

            })

            _.each(events, function (e) {
                e.timeline = _.sortBy(e.timeline, function (event) {
                    return event.getDate()
                })
            })


            store._vm.core.mtrx.kit.usersInfoForChats(m_chats).then(i => {
                commit('SET_EVENTS_TO_STORE', events)
            })

        },
    }
})

export default store;
