import {mapState} from 'vuex';

import list from '@/components/chat/list/index.vue'
import chatInput from '@/components/chat/input/index.vue'
import join from '@/components/chat/join/index.vue'
import metaUrlPreview from '@/components/chat/metaUrlPreview/index.vue'
import PNUrlPreview from '@/components/chat/PNUrlPreview/PNUrlPreview.vue'
import Api from '../../application/api'
import f from '../../application/functions'


export default {
  name: 'chat',
  props: {
    chat: Object,
    u: String,
  },

  components: {
    list,
    chatInput,
    join,
    metaUrlPreview,
    PNUrlPreview,
  },

  data: function () {
    return {
      loading: false,
      usersinfo: [],
      prevImage: undefined,
      selectedImg: String,
      metaDataPreview: {},
      metaPreview: false,
      metaPreviewUrl: String,
      filePreview: {},
      anyFilePreview: {},
      api: new Api(this, {pocketnet: 'https://pocketnet.app:8888'}),
      PNmetaPreview: false,
      PNDataPreview: {},
      timeout: null
    }

  },

  created() {

  },

  mounted() {
    this.getuserinfo()
  },

  destroyed() {
    this.$store.commit('SET_CURRENT_ROOM', false)
  },

  watch: {
    chat: {
      immediate: true,
      handler: function () {
        if (this.chat)
          this.$store.commit('SET_CURRENT_ROOM', this.chat.roomId)
        else
          this.$store.commit('SET_CURRENT_ROOM', false)
      }
    }
    //$route: 'getdata'
  },

  computed: mapState({
    auth: state => state.auth,
    m_chat: function () {

      if (this.chat && this.chat.roomId) {
        var m_chat = this.core.mtrx.client.getRoom(this.chat.roomId)
        console.log("CHAT", m_chat)
        return m_chat || {}
      }

    }

  }),

  methods: {
    previewImage: function (data) {
      this.prevImage = data.base64
    },
    newchat: function (chat) {
      this.$emit("newchat", chat)
    },

    sending: function () {
      this.$emit('sending')
    },

    getuserinfo: function () {
      if (this.u) {
        this.core.user.usersInfo(this.u).then(info => {
          this.usersinfo = info
        })
      }
    },
    membership: function () {

      if (this.m_chat) {
        return this.m_chat.getMyMembership()
      }

    },
    closing: function (e) {
      console.log('~~~CLOSING', e)
      this.metaPreview = e
    },
    usersinfoNames: function () {
      return _.map(this.usersinfo, function (u) {
        return u.name
      }).join(', ')
    },

    openImgM(img) {
      this.selectedImg = img
    },
    hidePreview(prev) {
      this.metaPreview = prev
      this.PNmetaPreview = false
      this.metaDataPreview = {}
    },
    getMetaUrl(url) {
      let ts = 0
      this.metaPreviewUrl = url
      this.core.mtrx.client.getUrlPreview(url, ts).then(response => {
        this.metaDataPreview = response
      }).catch(error => {
        console.log(error)
      })
      this.metaPreview = true
    },
    getFilePreview(data) {
      this.filePreview = data
    },
    anyFileData(data) {
      this.anyFilePreview.name = data.file.name
      this.anyFilePreview.size = data.file.size
      console.log(this.anyFilePreview, "any previewsadasd")
    },

    getPNrequestData(url) {
      const parsed_url = f.poketnetUrlParser(url)

      if (parsed_url.post) {
        return {
          api_method: 'postInfo',
          parameters: parsed_url.post
        }
      } else return {
        api_method: 'pocketNetProfile',
        parameters: parsed_url.user
      }

      // if(parsed_url.user) {
      //   return {
      //     api_method: 'pocketNetProfile', 
      //     parameters: parsed_url.user 
      //   }
      // }

    },

    async createPocketNetProfile(url) {
      const meta_data = this.getPNrequestData(url)
      const address = await this.api.pocketnet.pocketNetProfileAddress({parameters: meta_data.parameters})
      const response = await this.api.pocketnet.pocketNetProfileInfo({parameters: address[0].address})
      console.log('PROFILE RESPONSE~~~~~', response)

      this.PNDataPreview.header = response[0].name
      this.PNDataPreview.text = decodeURIComponent(response[0].a)
      this.PNDataPreview.userpic = response[0].i
      this.PNDataPreview.followers = response[0].subscribers.length
      this.PNDataPreview.following = response[0].subscribes.length
      this.PNDataPreview.reputation = Math.trunc(response[0].reputation)

      this.PNmetaPreview = true
    },

    async createPocketNetPost(url) {
      this.PNDataPreview.is_post = true
      const meta_data = this.getPNrequestData(url)
      let response = await this.api.pocketnet.postInfo({parameters: meta_data.parameters})
      console.log('POST RESPONSE~~~~~', response)

      const url_text = decodeURIComponent(f.getTxt(response[0].m))

      this.PNDataPreview.header = f.getUrl(url_text)
      this.PNDataPreview.name = response[0].userprofile.name
      this.PNDataPreview.text = url_text.length > 200 ? f.urlSeparator(f.textFormatter(url_text, 170)) : url_text
      this.PNDataPreview.userpic = response[0].userprofile.i
      this.PNDataPreview.tags = response[0].t.length > 5 ? response[0].t.slice(0, 5) : response[0].t
      this.PNDataPreview.date = f.dateParser(response[0].time)
      this.PNDataPreview.image = response[0].i[0]

      this.PNDataPreview.rating = {
        score: `${(response[0].scoreSum / response[0].scoreCnt).toFixed(1)} / ${response[0].scoreCnt}`,
        stars: f.setRating(parseInt(response[0].scoreSum), parseInt(response[0].scoreCnt))
      }

      let text_url = new URL(this.PNDataPreview.header)
      if (text_url.hostname === 'www.youtube.com') {
        this.PNDataPreview.video = `https://www.youtube.com/embed/${text_url.searchParams.get('v')}`
      } else if (text_url.hostname === 'vimeo.com') {
        this.PNDataPreview.video = `https://player.vimeo.com/video${text_url.pathname}`
        console.log('VIMEO!', this.PNDataPreview.video)
      } else {
        let url_preview = await this.core.mtrx.client.getUrlPreview(this.PNDataPreview.header, 0)

        this.PNDataPreview.text_url = {
          title: url_preview['og:title'],
          desctiption: f.textFormatter(url_preview['og:description'], 100)
        }
      }

      this.PNmetaPreview = true
    },

    async getPNMetaPreview(url) {
      const meta_data = this.getPNrequestData(url)
      this.metaPreview = false

      try {
        if (meta_data.api_method === 'postInfo') {
          this.createPocketNetPost(url)
          return
        }
        if (meta_data.api_method === 'pocketNetProfile') {
          this.createPocketNetProfile(url)
          return
        }
      } catch (error) {
        console.error(error)
      }
    },

    chatUrlHandler(url) {
      this.metaPreview = false

      if (this.timeout) {
        clearTimeout(this.timeout)
      }

      this.timeout = setTimeout(() => {
        var newURL = new URL(url)
        newURL.hostname === 'pocketnet.app' ? this.getPNMetaPreview(url) : this.getMetaUrl(url)
      }, 500)

    }
  }
}