import {mapState} from 'vuex';

import event from '@/components/events/event/index.vue'
import {PhotoSwipe, PhotoSwipeGallery} from 'v-photoswipe'
import * as _ from "underscore";
import imgPreview from '@/components/events/previews/imgPreview/index.vue'

export default {
  name: 'events',
  props: {
    events: Array,

    previewImage: {
      type: Object,
      default: function(){
        return {
          show: false
        }
      }
    },

    filePreview: Object,
  },
  components: {
    event,
    'v-photoswipe': PhotoSwipe,
    'v-photoswipe-gallery': PhotoSwipeGallery,
    imgPreview

  },
  data: function () {

    return {
      isOpen: false,
      isOpenGallery: false,
      type: '',
      windowWidth: 1000,

      options: {
        index: 0
      },
      optionsGallery: {},

      imgs: [],
      listImgs: [],
      loading: false,
      wasupdated: {},
      image_list: [],
    }

  },

  created: () => {

  },

  watch: {
    //$route: 'getdata'
  },

  computed: mapState({
    auth: state => state.auth
  }),

  updated: function () {


    var updated = {};

    _.each(this.events, (e) => {
      if (!this.wasupdated[e.event.event_id]) updated[e.event.event_id] = e
    })

    this.$emit('updated', updated)

    _.each(this.events, (e) => {
      this.wasupdated[e.event.event_id] = true
    })

  },

  methods: {


    showPhotoSwipe(index) {
      this.isOpen = true
      this.$set(this.options, 'index', index)
    },

    hidePhotoSwipe() {
      this.isOpen = false
    },
    removeEvent() {
      // console.log(this.core.mtrx.client)
      console.log(this.timeline)
    },
    galleryOpen(data) {
      this.imgs = this.events;
      this.listImgs = []

      _.each(this.imgs, event => {

        if (event.event.content.msgtype === 'm.image') {

          this.listImgs.push({
            src: event.event.content.url, 

            w: event.event.content.info.w, 
            h: event.event.content.info.h,

            eventId: event.event.event_id
          })

        }

      })

      let imgIndex = this.listImgs.map(function (e) {

        return e.eventId

      }).indexOf(data.event_id)

      this.showPhotoSwipe(imgIndex)
    },

    getWindowWidth(){

      this.windowWidth = document.documentElement.clientWidth
    }

  },

  mounted(){

    this.getWindowWidth();
    window.addEventListener('resize', this.getWindowWidth);

  },

  beforeDestroy(){

    window.removeEventListener('resize', this.getWindowWidth);

  }
}