<template>
  <div id="app">
    <MainWrapper />

    <transition name="fade">
      <fixedmessageicon v-if="iconshow" />
    </transition>
    
    <preloader v-if="loading" />

    <transition name="fade">
      <div class="fixedallwrapper offline" v-if="unauthorized">
        <div class="table">
          <div class="cell">
            <div class="icon">
              <i class="fas fa-wifi"></i>
            </div>
            <div class="label">
              Unauthorized
            </div>
          </div>
        </div>
      </div>
    </transition>

  </div>
</template>

<script>
  import MainWrapper from './components/main/index.vue'

  export default {
    name: 'App',
    components: {
      MainWrapper
    },

    computed : {
      unauthorized : function(){
        return this.$store.state.unauthorized
      },
      online : function(){
        return this.$store.state.online
      },
      loading : function(){
        return this.$store.state.loading
      },
      iconshow : function(){
        return this.$store.state.icon ? true : false
      },
    }
  }
</script>

<style  lang="sass" src="./index.sass"></style>

