import { mapState } from 'vuex';
// TODO IGOR' SEARCH ALL COMPONENTS
export default {
    name: 'search',
    props: {
        placeholder : String
    },

    data : function(){

        return {
            loading : false
        }

    },

    created : () => {

    },

    watch: {
        //$route: 'getdata'
    },
    computed: mapState({
        auth : state => state.auth,
    }),

    methods : {
        search : function(evt){
            var text = evt.target.value.toLowerCase()
            this.$emit('search', text)

        }
    },
}