import { mapState } from 'vuex';

export default {
    name: 'eventsMember',
    props: {
        event: Object,
        preview: Boolean,
        userinfo : Object
    },

    data : function(){

        return {
            loading : false
        }

    },

    computed: mapState({
        auth : state => state.auth,
        keyword : function(){

            var membership = this.event?.content.membership

            if (membership == 'join') return "joined in the chat"
            if (membership == 'invite') return "invited user in the chat"

            return "Leave"
        
        }
    }),

    methods : {
        
    },
}