var _ = require('lodash');
import f from "./functions";

class Notifier {
    constructor(core, p){
        if(!p) p = {}

        this.core = core
        
    }

    message (event, user){

        console.log('this.core.vm.$store.state.currentRoom', this.core.vm.$store.state.currentRoom, event.room_id)

        if (this.core.vm.$store.state.currentRoom == event.room_id || this.core.vm.$store.state.currentRoom == 'all') return

        this.show({

            title : 'New message from ' + user.name,
            message : event.content.body,
            icon : user.image

        }, '/chat?id=' + event.room_id)
    }

    event (event){

        var room = null;

        this.core.mtrx.isReaded(event, true).then(readed => {
            if(!this.core.mtrx.me(event.event.sender) && event.event.sender && !readed){

                this.core.user.usersInfo([f.getmatrixid(event.event.sender)]).then(info => {
        
                    if(info && info[0]){
                        if (event.event.type == 'm.room.message') {
                            this.message(event.event, info[0])
                        }
                    }
        
                })
        
            }
        })
        
    }

    show = function(info, click){

        if(typeof click != 'function'){

            var route = click

            click = () => {
                this.core.vm.$router.push(route)
            }
        }

        this.core.vm.$message({

            title : info.title,
            message : info.message,
            iconImg : info.icon || null,
            onClick : click,
            zIndex : 2900,
            supportHTML : true,
            wrapperClassName : "notificationWrapper",
            type : 'info'
      
        })
    }
}

export default Notifier