import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router);

import settings from '@/views/settings'
import contacts from '@/views/contacts'
import contact from '@/views/contact'
import chats from '@/views/chats'
import chat from '@/views/chat'

let router = new Router({
  routes: [
    /*{
      path: '/',
      name: 'contacts',
      component: ContactList
    },
    {
      path: '/contact',
      name: 'contact',
      component: ContactList,
      props: true
    },*/
    
    {
      path: '/contacts',
      name: 'contacts',
      component: contacts
    },
    {
      path: '/contact',
      name: 'contact',
      component: contact
    },
    {
      path: '/chats',
      name: 'chats',
      component: chats
    },
    {
      path: '/chat',
      name: 'chat',
      component: chat
    },

    {
      path: '/settings',
      name: 'settings',
      component: settings
    },
    
    /*{
      path: '/chat',
      name: 'chat',
      component: UserChat,
      props: true
    }*/
  ],
  mode: 'history',
  scrollBehavior (to, from, savedPosition) {
    return { x: 0, y: 0 }
  }
})

export default router;
