
var _ = require('lodash');
const SHA2 = require("sha2");
import f from "./functions";
class MTRXKIT {

  constructor(core, p) {
    if (!p) p = {}

    this.core = core
  }

  tetatetid (user1, user2){

    var seed = 2;

    var id = parseInt(user1.id, 16) * parseInt(user2.id, 16) * seed
    var hash = SHA2.sha224(id.toString()).toString('hex')

    return hash
  }

  usersInfoForChats (m_chats){

    /// TODO FILTER CONTACTS

    var members = []

    _.each(m_chats, (chat) => {
      members = members.concat(_.toArray(chat.currentState.members))
    })
  
    return this.usersInfo(members)
  }

  usersInfo (members){
    var ids = _.map(members, function(m){
      return f.getmatrixid(m.name)
    })

    return this.core.user.usersInfo(ids)
  }

}

export default MTRXKIT