import {mapState} from 'vuex';
import actions from "@/components/events/event/actions/index.vue";
import metaMessage from "@/components/events/event/metaMessage/index.vue";
import filePreview from "@/components/events/previews/filePreview/index.vue";
import fileMessage from "@/components/events/event/fileMessage/index.vue";
import f from '@/application/functions'
import VuePictureSwipe from 'vue-picture-swipe';

export default {
  name: 'eventsMessage',
  props: {
    event: Object,
    preview: Boolean,
    userinfo: Object,
    readed: Boolean,
    baseImg: {
      type: String,
      default: 'empty'
    },
    filePreview: Object,
    imgEvent: {},
    add_image: Function,
    goToGallery: Function,
    windowWidth: Number
  },

  data: function () {

    return {
      loading: false,
      msgActions: false,
      margin: '.6em auto 0 2em',
      isActive: false,
      color: "red",
      size: "100px",
      anyMetaUrl: "",
      fileMsg: {},
      sizeMB: Number,
      initImageWidth: 304,
      imageFactor: 0.6
    }

  },
  components: {
    actions,
    metaMessage,
    filePreview,
    fileMessage,
    VuePictureSwipe
  },
  computed: {...mapState({
    auth: state => state.auth,
    my: function () {
      return this.userinfo.id === this.core.user.userinfo?.id
    },

    imageWidth(){

      return this.windowWidth < 650 ? this.imageFactor * this.initImageWidth : this.initImageWidth;
    },

    imageHeight(){
      const {h, w} = this?.event?.content?.info;
      const ratio = w / this.imageWidth;

      return h / ratio || 300;
    },


    previewImageUrl(){

      console.log('imageUrl');

      return this.core.mtrx.client.mxcUrlToHttp(this.metaContent['og:image']);
      
    },

    metaContent(){

      if (this.event.content.msgtype === 'm.metaLink') {

        return JSON.parse(this.event.content.body)

      }

      return {};
    },

    imageUrl(){

      return this.event.content?.url;
    }


  }),
  
},

  mounted: function () {

    console.log('mounted???')


    if (this.event.content.msgtype === 'm.file') {
      this.getFileData(this.event.content.body)
      this.sizeMB = f.formatBytes(this.fileMsg.size)
    }
  },
  methods: {
    openImage(img) {
      this.$emit('openImg', img)
    },

    clickedOutside() {
      this.msgActions = false
      this.isActive = false
    },

    format_date(value) {
      if (value) {
        return new Date(value).toLocaleTimeString([], {hour: '2-digit', minute: "2-digit", hour12: false})
      }
    },

    handleMsgActionsList() {
      this.msgActions = !this.msgActions
      this.isActive = !this.isActive
    },

    getFileData(data) {
      console.log(data, "asdkjnsakjdnjksandjkaskjdksan")
      this.fileMsg = JSON.parse(data)
      this.sizeMB = this.fileMsg.size
    },

    openImageGallery(msgEvent){
      this.$emit('openGalleryImg', msgEvent )
    },
    

  }
}