import moment from 'moment'
import f from './functions'
import Axios from './axios'
import _ from 'underscore'
import axios from "axios";

var {error} = require('./error')

var Api = function (core, servers) {

  var self = this;
  var a = new Axios();

  var cache = {}
  var loading = {}


  var scasheAct = function (ids, key, resultsKey) {

    var waitLoading = {}

    if (!_.isArray(ids)) ids = [ids]

    if (!resultsKey) resultsKey = key

    if (!cache[key]) {
      cache[key] = {}
    }

    if (!loading[key]) {
      loading[key] = {}
    }

    var idtoloadPrev = _.uniq(_.filter(ids, function (id) {
      return !cache[key][id]
    }))

    var idtoload = _.filter(idtoloadPrev, function (id) {

      if (!loading[key][id]) {
        loading[key][id] = true
        return true
      }

      waitLoading[id] = true

    })


    var handleResults = function (result) {

      _.each(result, function (row) {

        if (row[resultsKey]) {
          cache[key][row[resultsKey]] = row

        }

      })

      var nresult = {};

      return f.pretry(() => {

        _.each(ids, function (id) {

          if (cache[key][id]) {
            nresult[id] = (cache[key][id])

            delete loading[key][id]
            delete waitLoading[id]
          }

        })

        return _.toArray(waitLoading).length == 0

      }).then(() => {
        return Promise.resolve(nresult)
      })

    }


    return {
      id: idtoload,
      handle: handleResults
    }
  }

  var waitonline = function () {

    if (!core || !core.waitonline) {
      return Promise.resolve()
    }

    return core.waitonline()

  }

  var crequest = function (ids, key, rkey) {

    var sh = scasheAct(ids, key, rkey)

    if (!sh.id.length) {
      return sh.handle([])
    }

    return Promise.reject(sh)
  }

  var request = function (data, to) {

    return waitonline().then(() => {

      data || (data = {})

      return a.axios({
        to,
        data,
      }).then(r => {

        return Promise.resolve(r)

      })
        .catch(e => {

          if (e == 'noresponse') {

            return new Promise((resolve, reject) => {

              setTimeout(function () {
                request(data, to).then(r => {


                  return resolve(r)

                }).catch(e => {

                  return reject(e)

                })

              }, 3000)
            })

          }

          return Promise.reject(e)

        })


    })


  }


  self.clearCache = function (key) {

    if (!key) {
      cache = {}
    } else {
      delete cache[key]
    }


  }

  self.pocketnet = {

    common: (data, method) => {
      const args = JSON.stringify(JSON.stringify(data))

      if (!data) data = {}

      data.method = method
      data.node = '185.148.147.15'

      data.parameters = f.hexEncode(JSON.stringify(data.parameters || ""))

      return request(data, servers.pocketnet + '/rpc-' + (method || "common"))
    },

    userState: (addresses) => {

      if (!_.isArray(addresses)) addresses = [addresses]

      var parameters = [addresses];

      return self.pocketnet.common({parameters}, 'getuserstate')
    },

    search: (text) => {

      var parameters = [text, 'users'];

      return self.pocketnet.common({parameters}, 'search2').then(data => {

        // console.log("data", data)

        return Promise.resolve(data.users?.data || [])
      })
    },

    userInfo: (addresses) => {
    
      return crequest(addresses, 'pocketnet_userInfo', 'address').catch(sh => {
    
        var parameters = [sh.id];
    
        return self.pocketnet.common({parameters}, 'getuserprofile').then(results => {

          return sh.handle(results)
    
        })
      })
    },
    
    postInfo: (parameters) => {

      return crequest(parameters, 'pocketnet_postInfo', 'address').catch(sh => {

          var parameters = [[sh.id[0].parameters]]
        
          return self.pocketnet.common({parameters}, 'getrawtransactionwithmessagebyid').then(results => {
        
            sh.handle(results)
            return Promise.resolve(results)
    
        })
      })
    },
    
    pocketNetProfileAddress: (profile_name) => {

      return crequest(profile_name, 'pocketnet_profileAddress', 'address').catch(sh => {

        var parameters = [[sh.id[0].parameters]]
      
        return self.pocketnet.common({parameters}, 'getuseraddress').then(results => {
          
          sh.handle(results)
          return Promise.resolve(results)
    
      })
    })
  },

    pocketNetProfileInfo: (profile_address) => {

      return crequest(profile_address, 'pocketnet_profileInfo', 'address').catch(sh => {

        var parameters = [[sh.id[0].parameters]]
      
        return self.pocketnet.common({parameters}, 'getuserprofile').then(results => {

          sh.handle(results)
          return Promise.resolve(results)
    
        })
      })
    },

    }
    
    
    return self;
  }
    
    export default Api
    
