
import FooterChat from '../layouts/footerChat/index.vue'
import {mapState} from 'vuex'

export default {
  name: "mainWrapper",
  components: {
    FooterChat
  },
  data: function () {

    return {
      prevRoute: null,
      loading: false
    }

  },

  created: () => {

  },

  watch: {
    //$route: 'getdata'
  },

  beforeRouteEnter(to, from, next) {
    next(vm => {
      vm.prevRoute = from
    })
  },

  beforeRouteUpdate(to, from, next) {
    next()
  },

  computed: mapState({
    auth: state => state.auth,
    ...mapState([
      'currentUserChat'
    ]),

    showFooter : function(){
      return this.$route.name != 'chat'
    }

  }),

  methods: {},
}