import { mapState } from 'vuex';
import chatPreview from '@/components/chat/preview/index.vue'
export default {
    name: 'chatJoin',
    props: {
        chat : Object,
        usersinfo : Array
    },

    components : {
        chatPreview
    },

    data : function(){

        return {
            loading : false
        }

    },

    created : () => {

    },

    watch: {
        //$route: 'getdata'
    },
    computed: mapState({
        auth : state => state.auth,
    }),

    methods : {
        join : function(){
            this.core.mtrx.client.joinRoom(this.chat.roomId).then(() => {
                this.$emit('joined')
            });
        },

        decline : function(){
            this.$router.go(-1)
        }
    },
}