import { mapState } from 'vuex';

export default {
    name: 'contactActions',
    props: {
        contact : Object
    },

    data : function(){

        return {
            loading : false
        }

    },

    created : () => {

    },

    watch: {
        //$route: 'getdata'
    },
    computed: mapState({
        auth : state => state.auth,
        tetatetid : function(){
            return this.core.mtrx.kit.tetatetid(this.contact, this.core.user.userinfo)
        }
    }),

    methods : {
        chat : function(){

            this.core.mtrx.kit.tetatetid(this.contact, this.core.user.userinfo)

        }
    },
}